import React, {Component, Fragment} from 'react';
import classname from 'classnames';
import _ from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import ms from 'ms';
import swal from 'sweetalert';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import NetworkOnIcon from '@material-ui/icons/Wifi';
import NetworkOffIcon from '@material-ui/icons/WifiOff';
import SettingsIcon from '@material-ui/icons/Settings';
import TextField from '@material-ui/core/TextField';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import Switch from '@material-ui/core/Switch';
import Avatar from '@material-ui/core/Avatar';
import deepPurple from '@material-ui/core/colors/deepPurple';

import './App.css';
import WebSocketProxy, {GameStateType, MqttClientMeta} from './WebSocketProxy';
import SettingDialog from './SettingDialog';
import TimeoutFullScreen from './TimeoutFullScreen';

const TIME_REMAIN_STEP = ms('1s');

function __showSnackbar(title: string, contentString: string) {
    const contentOptions = {
        element: 'textarea',
        attributes: {
            value: contentString,
        },
    };
    const swalOptions = {
        title,
        // icon: 'info',
        content: contentOptions,
        // buttons: [],
        // timer: 3000,
    };
    swal(swalOptions);
}

function __convertVisibleMS(value: any) {
    try {
        return ms(value);
    } catch (e) {
        console.error(e);
        __showSnackbar(e.message, e.stack);
    }
    return '';
}

function _saveClientFromStorage(client: {id: string, name: string}) {
    sessionStorage.setItem('clientId', client.id);
    sessionStorage.setItem('clientName', client.name);
}

function _loadClientFromStorage(): {id: string, name: string} {
    const clientId = sessionStorage.getItem('clientId');
    const clientName = sessionStorage.getItem('clientName');
    const defaultId = 'you-turn_' + Math.random().toString(16).substr(2, 8);
    const client =  {
        id: clientId || defaultId,
        name: clientName || defaultId,
    };
    _saveClientFromStorage(client);
    return client;
}

const styles = (theme: any) => ({
    root: {
        flexGrow: 1,
    },
    grow: {
        flexGrow: 1,
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    button: {
        margin: theme.spacing.unit,
    },
    bigButton: {
        margin: theme.spacing.unit,
        width: "95%",
        height: "32%",
    },
    purpleAvatar: {
        margin: 10,
        color: '#fff',
        backgroundColor: deepPurple[500],
    },
    row: {
        display: 'flex',
        justifyContent: 'center',
    },
    clientPanel: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
    }
});

function __replaceSpeechText(text: string, replaceObject: {name: string}) {
    let result = String(text);
    _.forEach(replaceObject, (value, key) => {
        result = result.replace('${' + key + '}', value);
    });
    return result;
}

interface IAppProps {
    classes: any,
}

interface IAppState {
    clients: Array<MqttClientMeta>,
    clientName: string,
    clientId: string,
    channel: string,
    gameState: GameStateType,
    currentClientId: string,
    connected: boolean,
    thinkTimeOut: number,
    delayTimeOut: number,
    settingDialog: boolean,
    turnTimeoutDialog: boolean,
    turnMeSpeechText: string,
    timeoutSpeechText: string,
    delayTurnMeSpeechText: string,
    timeoutTimer?: NodeJS.Timeout,
    timeRemain: number,
    timeRemainTimer?: NodeJS.Timeout,
    epic: number,
    nextEpicDialog: boolean,
}

class App extends Component<IAppProps, IAppState> {
    wsProxy: React.RefObject<WebSocketProxy>;
    audioComponent: React.RefObject<HTMLAudioElement>;
    state = {
        clients: [],
        clientId: _loadClientFromStorage().id,
        clientName: _loadClientFromStorage().name,
        channel: 'testchannel',
        gameState: GameStateType.end,
        currentClientId: '',
        connected: false,
        thinkTimeOut: 0,
        delayTimeOut: 0,
        settingDialog: false,
        turnTimeoutDialog: false,
        delayTurnMeSpeechText: '${name} ',
        turnMeSpeechText: '${name} ',
        timeoutSpeechText: '${name} ',
        timeoutTimer: undefined,
        timeRemain: 0,
        timeRemainTimer: undefined,
        epic: 0,
        nextEpicDialog: false,
    };

    constructor(props: any) {
        super(props);
        this.wsProxy = React.createRef();
        this.audioComponent = React.createRef();
        this.handelChangeClient = this.handelChangeClient.bind(this);
        this.renderClients = this.renderClients.bind(this);
        this.handelTurnTo = this.handelTurnTo.bind(this);
        this.handleTurnYou = this.handleTurnYou.bind(this);
        this.handleCloseTurnTimeoutDialog = this.handleCloseTurnTimeoutDialog.bind(this);
        this.clearTimeoutTimer = this.clearTimeoutTimer.bind(this);
        this.setTimeoutTimer = this.setTimeoutTimer.bind(this);
        this.countdownTimer = this.countdownTimer.bind(this);
        this.playTTS = this.playTTS.bind(this);
    }

    componentDidMount() {
        // Auto connected
        // if(this.wsProxy.current) {
        //     this.wsProxy.current.open(this.state.channel);
        // }
    }

    handelChangeClient(clients: Array<MqttClientMeta>) {
        this.setState(() => ({clients}));
    }

    handelTurnTo(clientId: string) {
        this.setState(() => ({currentClientId: clientId}));
    }

    handleCloseTurnTimeoutDialog() {
        this.setState(() => ({turnTimeoutDialog: false}));
    }
    handleOpenTurnTimeoutDialog() {
        this.setState(() => ({turnTimeoutDialog: true}));
    }
    handleCloseNextEpicDialog() {
        this.setState(() => ({nextEpicDialog: false}));
    }
    handleOpenNextEpicDialog() {
        this.setState(() => ({nextEpicDialog: true}));
    }

    playTTS(speechText: string, timeout:number = 0) {
        if(this.state.gameState !== GameStateType.play) {
            return;
        }

        // play
        if(this.audioComponent.current) {
            const q = encodeURIComponent(speechText);
            // const src = 'http://translate.google.com/translate_tts?ie=UTF-8&total=1&idx=0&textlen=32&client=tw-ob&tl=en-gb&q=' + q;
            // const src = 'http://translate.google.com/translate_tts?ie=UTF-8&total=1&idx=0&client=tw-ob&tl=zh-tw&q=' + q;
            const src = `https://google-translate-proxy.herokuapp.com/api/tts?query=${q}&language=zh-TW&speed=0.5`;
            this.audioComponent.current.setAttribute('src',  src);
            this.audioComponent.current.load();
            this.audioComponent.current.play();
        }
    }

    countdownTimer(timeRemain: number) {
        const newTimeRemain = timeRemain - TIME_REMAIN_STEP;

        const timer = setTimeout(() => {
            if(newTimeRemain > 0) {
                this.countdownTimer(newTimeRemain)
            } else {
                this.setState(() => ({
                    timeRemain: 0,
                    timeRemainTimer: undefined,
                }));
            }
        }, TIME_REMAIN_STEP);

        this.setState(() => ({
            timeRemain,
            timeRemainTimer: timer,
        }));
    }

    setTimeoutTimer(callback: () => void, timeout: number) {
        if(this.state.gameState !== GameStateType.play) {
            return;
        }

        this.clearTimeoutTimer(() => {
            const timeoutTimer = setTimeout(callback, timeout);
            this.countdownTimer(timeout);
            this.setState(() => ({
                timeoutTimer,
                timeRemain: timeout,
            }));
        });
    }

    clearTimeoutTimer(callback?: () => void) {
        // timeoutTimer
        if(this.state.timeoutTimer) {
            clearTimeout(this.state.timeoutTimer);
        }

        // timeRemainTimer
        if(this.state.timeRemainTimer) {
            clearTimeout(this.state.timeRemainTimer);
        }

        this.setState(() => ({
            timeoutTimer: undefined,
            timeRemainTimer: undefined,
        }), callback);
    }

    handleTurnYou(timeout = this.state.thinkTimeOut) {
        if(this.state.gameState !== GameStateType.play) {
            return;
        }

        const clientName = this.state.clientName;

        // Show: turn me
        console.log('Turn me: ' + clientName);
        const turnMeText = __replaceSpeechText(this.state.turnMeSpeechText,{name: clientName});
        this.playTTS(turnMeText);

        this.handleOpenTurnTimeoutDialog();

        // Show: timeout
        this.setTimeoutTimer(() => {
            const speechText = __replaceSpeechText(this.state.timeoutSpeechText,{name: clientName});
            this.playTTS(speechText);
        }, timeout);
    }

    handleDelayTurnYou(timeout = this.state.delayTimeOut) {
        if(this.state.gameState !== GameStateType.play) {
            return;
        }

        const clientName = this.state.clientName;

        // Show: turn me
        const turnMeText = __replaceSpeechText(this.state.delayTurnMeSpeechText,{name: clientName});
        this.playTTS(turnMeText);

        // Show: timeout
        this.setTimeoutTimer(() => {
            const speechText = __replaceSpeechText(this.state.timeoutSpeechText,{name: clientName});
            this.playTTS(speechText);
        }, timeout);
    }

    renderClients(clients: Array<MqttClientMeta>) {
        const {classes} = this.props;
        const Items: any[] = [];
        clients.forEach(client => {
            Items.push((
                <ExpansionPanel key={client.id}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography variant="body1">
                            <span className={classname({'App-highlight': (client.id === this.state.currentClientId)})}>({client.epic},{client.order}) {client.name} - {client.id}</span>
                        </Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <div>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaClientEpic(client.id,-1);
                            }}>
                                <RemoveCircleIcon />
                            </IconButton>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaClientEpic(client.id,1);
                            }}>
                                <AddCircleIcon />
                            </IconButton>
                            <Switch
                                checked={client.enable}
                                onClick={() => {
                                    this.wsProxy.current && this.wsProxy.current.toggleEnable(client.id);
                                }}
                                value={client.enable}
                            />
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaOrder(client.id, -1);
                            }}>
                                <ArrowUpwardIcon />
                            </IconButton>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaOrder(client.id, 1);
                            }}>
                                <ArrowDownwardIcon />
                            </IconButton>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.exit(client.id);
                            }}>
                                <DeleteIcon />
                            </IconButton>
                        </div>
                    </ExpansionPanelDetails>
                </ExpansionPanel>));
        });

        return (
            <div className={classes.clientPanel}>
                {Items}
            </div>
        );
    }

    renderGameState(state: GameStateType) {
        if(state === GameStateType.play) {
            return "Play";
        }
        if(state === GameStateType.ready) {
            return "Ready";
        }
        if(state === GameStateType.end) {
            return "End";
        }
        if(state === GameStateType.pause) {
            return "Pause";
        }
        return "End";
    }

    render() {
        const {classes} = this.props;
        return (
            <div className="App">
                <AppBar position="static">
                    <Toolbar>
                        {this.renderGameState(this.state.gameState)}
                        <Typography variant="title" color="inherit" className={classes.grow}>
                            {this.state.clientName}
                        </Typography>
                        <Typography variant="caption" color="inherit" className={classes.grow}>
                            {this.state.clientId}
                        </Typography>
                        {this.state.connected && <IconButton color="inherit" onClick={()=>{
                            this.setState(() => ({settingDialog: true}));
                        }}><SettingsIcon/></IconButton>}
                    </Toolbar>
                </AppBar>
                <Grid container spacing={16}>
                    <Grid item xs={12}>
                        <ExpansionPanel>
                            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                                <Typography className={classes.heading}>
                                    {this.state.connected ? <NetworkOnIcon/> : <NetworkOffIcon/>}
                                    
                                </Typography>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <Grid container spacing={16}>
                                    <Grid item xs={12}>
                                        <TextField
                                            id="network-channel"
                                            fullWidth
                                            label="Channel"
                                            value={this.state.channel}
                                            defaultValue={this.state.channel}
                                            className={classes.textField}
                                            margin="normal"
                                            onChange={event => {
                                                const value = event.target.value;
                                                this.setState(() => ({channel: value}));
                                            }}
                                        />
                                        <Button variant="contained" color="primary" className={classes.button} onClick={() => {
                                            if(this.wsProxy.current) {
                                                this.wsProxy.current.open(this.state.channel);
                                            }
                                        }}>
                                            Open
                                        </Button>
                                        <Button variant="contained" className={classes.button} onClick={() => {
                                            if(this.wsProxy.current) {
                                                this.wsProxy.current.exit(this.state.clientId);
                                                this.wsProxy.current.close();
                                            }
                                        }}>
                                            Close
                                        </Button>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <TextField
                                            id="game-name"
                                            fullWidth
                                            label=""
                                            value={this.state.clientName}
                                            defaultValue={this.state.clientName}
                                            className={classes.textField}
                                            margin="normal"
                                            onChange={event => {
                                                const value = event.target.value;
                                                this.setState(() => ({clientName: value}));
                                            }}
                                        />
                                        <Button variant="contained" color="primary" className={classes.button} onClick={() => {
                                            if(this.wsProxy.current) {
                                                _saveClientFromStorage({id: this.state.clientId, name: this.state.clientName});
                                                this.wsProxy.current.join(this.state.clientId, this.state.clientName);
                                            }
                                        }}>
                                            
                                        </Button>
                                    </Grid>
                                </Grid>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>
                    </Grid>
                    <Grid item xs={12}>
                        <Button className={classes.button} onClick={() => {
                            if(this.wsProxy.current) {
                                this.wsProxy.current.ready();
                            }
                        }}>Ready</Button>
                        <Button className={classes.button} onClick={() => {
                            if(this.wsProxy.current) {
                                this.wsProxy.current.end();
                            }
                        }}>End</Button>
                        <Button className={classes.button} onClick={() => {
                            if(this.wsProxy.current) {
                                this.wsProxy.current.play();
                            }
                        }}>Play</Button>
                        <Button className={classes.button} onClick={() => {
                            if(this.wsProxy.current) {
                                this.wsProxy.current.pause();
                            }
                        }}>Pause</Button>
                        <Button className={classes.button} onClick={() => {
                            if(this.wsProxy.current) {
                                this.wsProxy.current.next();
                            }
                        }}>Next</Button>
                    </Grid>
                    <Grid item xs={12}>
                        <div className={classes.row}>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaEpic(-1);
                            }}>
                                <RemoveCircleIcon />
                            </IconButton>
                            <Avatar className={classes.purpleAvatar}>{this.state.epic}</Avatar>
                            <IconButton onClick={() => {
                                this.wsProxy.current && this.wsProxy.current.deltaEpic(1);
                            }}>
                                <AddCircleIcon />
                            </IconButton>
                        </div>
                    </Grid>
                    <Grid item xs={12}>
                        {this.renderClients(this.state.clients)}
                    </Grid>
                </Grid>
                <WebSocketProxy
                    clientId={this.state.clientId}
                    ref={this.wsProxy}
                    onTurnTo={this.handelTurnTo}
                    onChangeClients={this.handelChangeClient}
                    onChangeGameState={state => {
                        this.setState(() => ({gameState: state}));
                    }}
                    onTurnYou={this.handleTurnYou}
                    onNotTurnYou={() => {
                        this.clearTimeoutTimer();
                        this.handleCloseTurnTimeoutDialog();
                    }}
                    onChangeConnected={connected => {
                        this.setState(() => ({connected}));
                    }}
                    onChangeThinkTimeOut={timeout => {
                        this.setState(() => ({thinkTimeOut: timeout}));
                    }}
                    onChangeDelayTimeOut={timeout => {
                        this.setState(() => ({delayTimeOut: timeout}));
                    }} onError={(e) => {__showSnackbar(e.message, e.stack || '');}}
                    onChangeEpic={epic => {
                        this.setState(() => ({epic}));
                    }}
                    onEmptyClientInEpic={() => {
                        this.handleOpenNextEpicDialog();
                    }} />
                <audio id="player" src="#" ref={this.audioComponent}/>
                <SettingDialog open={this.state.settingDialog} onClose={() => {
                    this.setState(() => ({settingDialog: false}))
                }} onSave={() => {
                    if(this.wsProxy.current) {
                        this.wsProxy.current.changeTimeout({
                            thinkTimeOut: this.state.thinkTimeOut,
                            delayTimeOut: this.state.delayTimeOut,
                        });
                    }
                }}>
                    <TextField
                        id="setting-think-timeout"
                        fullWidth
                        label=""
                        value={this.state.thinkTimeOut}
                        defaultValue={this.state.thinkTimeOut}
                        className={classes.textField}
                        helperText={__convertVisibleMS(this.state.thinkTimeOut)}
                        margin="normal"
                        onChange={event => {
                            const value = event.target.value;
                            this.setState(() => ({thinkTimeOut: +value}));
                        }}
                    />
                    <TextField
                        id="setting-delay-timeout"
                        fullWidth
                        label=""
                        value={this.state.delayTimeOut}
                        defaultValue={this.state.delayTimeOut}
                        className={classes.textField}
                        helperText={__convertVisibleMS(this.state.delayTimeOut)}
                        margin="normal"
                        onChange={event => {
                            const value = event.target.value;
                            this.setState(() => ({delayTimeOut: +value}));
                        }}
                    />
                    <TextField
                        fullWidth
                        label=""
                        value={this.state.turnMeSpeechText}
                        defaultValue={this.state.turnMeSpeechText}
                        className={classes.textField}
                        helperText={'${name}: '}
                        margin="normal"
                        onChange={event => {
                            const value = event.target.value;
                            this.setState(() => ({turnMeSpeechText: value}));
                        }}
                    />
                    <TextField
                        fullWidth
                        label=""
                        value={this.state.timeoutSpeechText}
                        defaultValue={this.state.timeoutSpeechText}
                        className={classes.textField}
                        helperText={'${name}: '}
                        margin="normal"
                        onChange={event => {
                            const value = event.target.value;
                            this.setState(() => ({timeoutSpeechText: value}));
                        }}
                    />
                    <TextField
                        fullWidth
                        label=""
                        value={this.state.delayTurnMeSpeechText}
                        defaultValue={this.state.delayTurnMeSpeechText}
                        className={classes.textField}
                        helperText={'${name}: '}
                        margin="normal"
                        onChange={event => {
                            const value = event.target.value;
                            this.setState(() => ({delayTurnMeSpeechText: value}));
                        }}
                    />
                </SettingDialog>
                <TimeoutFullScreen title={':' + ms(this.state.timeRemain)} open={this.state.turnTimeoutDialog} onClose={this.handleCloseTurnTimeoutDialog}>
                    <Button className={classes.bigButton} variant="contained" onClick={() => {
                        this.clearTimeoutTimer();
                        this.handleDelayTurnYou();
                    }}>
                        
                    </Button>
                    <Button className={classes.bigButton} variant="contained" color="primary" onClick={() => {
                        this.clearTimeoutTimer();
                        if(this.wsProxy.current) {
                            this.wsProxy.current.next();
                        }
                        this.handleCloseTurnTimeoutDialog();
                    }}>
                        
                    </Button>
                    <Button className={classes.bigButton} variant="contained" color="secondary" onClick={() => {
                        this.clearTimeoutTimer();
                        if(this.wsProxy.current) {
                            this.wsProxy.current.nextClientEpic(this.state.clientId, 1);
                            this.wsProxy.current.next();
                        }
                        this.handleCloseTurnTimeoutDialog();
                    }}>
                        
                    </Button>
                </TimeoutFullScreen>
                <TimeoutFullScreen title={''} open={this.state.nextEpicDialog} onClose={this.handleCloseNextEpicDialog}>
                    <Button className={classes.bigButton} variant="contained" color="primary" onClick={() => {
                        this.wsProxy.current && this.wsProxy.current.deltaEpic(1);
                        this.wsProxy.current && this.wsProxy.current.next();
                        this.handleCloseNextEpicDialog();
                    }}>
                        
                    </Button>

                </TimeoutFullScreen>
            </div>
        );
    }
}

export default withStyles(styles)(App);
