import mqtt from 'mqtt';
import React, { Component } from 'react';
import {object} from "prop-types";
import EventEmitter from 'eventemitter3'
import _ from 'lodash';
import ms from 'ms';

const DEFAULT_ORDER = 1;
const DEFAULT_EPIC = 1;
const DEFAULT_ENABLE = true;
const KEEP_ALIVE = ms('5h') / 1000; // sec

export enum GameStateType {
    ready = 'GameStateTypeReady',
    play = 'GameStateTypePlay',
    pause = 'GameStateTypePause',
    end = 'GameStateTypeEnd',
}

export enum MessageType {
    unknown = 'unknown',
    turnTo = 'turnTo',
    gameStateChange = 'gameStateChange',
}

export interface MqttClientMeta {
    id: string,
    name: string,
    order: number,
    epic: number,
    enable: boolean,
}

interface TurnInfo {
    currentClientId: string,
    nextClientId: string,
}

interface MessageMeta {
    clientId: string,
    channel: string,
}

class NodeMessage {
    channel = '';
    type?: MessageType = MessageType.unknown;
    readonly clientId: string;
    constructor({channel, type, clientId}: {channel: string, type?: MessageType, clientId: string}) {
        this.channel = channel;
        this.clientId = clientId;
        if(this.type) {
            this.type = type;
        }
    }
    toObject() : object {
        return {
            type: this.type,
            channel: this.channel,
            clientId: this.clientId,
        };
    }
    toString() : string {
        return JSON.stringify(this.toObject());
    }
}

class GameContextMessage extends NodeMessage {
    type = MessageType.gameStateChange;
    state = GameStateType.ready;
    clients: Array<MqttClientMeta> = [];
    currentClientId: string = '';
    thinkTimeOut: number = ms('5s');
    delayTimeOut: number = ms('2s');
    epic: number = DEFAULT_EPIC;

    constructor(context: any) {
        super(context);
        if(context && context.state) {
            this.state = context.state;
        }

        if(context && context.clients) {
            this.clients = context.clients;
        }

        if(context && context.currentClientId) {
            this.currentClientId = context.currentClientId;
        }

        if(context && context.thinkTimeOut) {
            this.thinkTimeOut = context.thinkTimeOut;
        }

        if(context && context.delayTimeOut) {
            this.delayTimeOut = context.delayTimeOut;
        }

        if(context && _.isNumber(context.epic)) {
            this.epic = context.epic;
        }
    }
    toObject() {
        const org = super.toObject();
        return {
            ...org,
            state: this.state,
            clients: this.clients,
            currentClientId: this.currentClientId,
            thinkTimeOut: this.thinkTimeOut,
            delayTimeOut: this.delayTimeOut,
            epic: this.epic,
        };
    }
    getHost() {
        return this.clientId;
    }
}

class TurnToMessage extends NodeMessage {
    type = MessageType.turnTo;
    turnToClientId: string = '';
    constructor(context: any) {
        super(context);
        if(context.turnToClientId) {
            this.turnToClientId = context.turnToClientId;
        }
    }
    toObject() {
        const org = super.toObject();
        return {...org, turnToClientId: this.turnToClientId};
    }
}

class GameStateManager {
    channel = '';
    clientId = '';
    context: GameContextMessage = new GameContextMessage(this.getMeta());

    constructor({channel, clientId} : MessageMeta) {
        this.channel = channel;
        this.clientId = clientId;

        this.context =  new GameContextMessage(this.getMeta());
    }

    private getMeta(): MessageMeta {
        return {
            channel: this.channel,
            clientId: this.clientId,
        };
    }

    loadCurrentState(state: GameContextMessage) {
        if(!(state instanceof GameContextMessage)) {
            throw new Error('can not load current state');
        }
        this.context = state;
        return this.context;
    }
    joinClient(clientId: string, clientName: string) {
        const foundClient = _.find<MqttClientMeta>(this.context.clients, (client) => client.id === clientId);
        if(!foundClient) {
            this.context.clients.push({
                id: clientId,
                name: clientName,
                order: this.context.clients.length + DEFAULT_ORDER,
                epic: this.context.epic,
                enable: DEFAULT_ENABLE,
            });
        }
        return this.context;
    }
    exitClient(clientId: string) {
        const foundClient = _.find<MqttClientMeta>(this.context.clients, (client) => client.id === clientId);
        if(foundClient) {
            _.pull(this.context.clients, foundClient);
        }
        return this.context;
    }

    getStateMessage(): string {
        return this.context.toString();
    }
    changeToReady() {
        this.context.state = GameStateType.ready;
        return this.context;
    }
    changeToEnd() {
        this.context.state = GameStateType.end;
        return this.context;
    }
    changeToPause() {
        this.context.state = GameStateType.pause;
        return this.context;
    }
    changeToPlay() {
        this.context.state = GameStateType.play;
        return this.context;
    }
    changeTimeout({thinkTimeOut, delayTimeOut} : {thinkTimeOut: number, delayTimeOut: number}) {
        this.context.thinkTimeOut = thinkTimeOut;
        this.context.delayTimeOut = delayTimeOut;
        return this.context;
    }

    /**
     *
     * @return {TurnInfo | undefined}
     * @throws Error
     */
    getTurnInfo(): TurnInfo | undefined {
        if(this.context.clients.length === 0) {
            throw new Error('At least one client');
        }

        const currentClientId = this.context.currentClientId;

        // candidate clients
        const candidateClients = this.context.clients.filter(client => client.enable && (this.context.epic === client.epic));
        if(candidateClients.length === 0) {
            return;
        }

        let index = candidateClients.map(client => client.id).indexOf(currentClientId);
        index = (index === -1) ? 0 : index;

        const clientLength = candidateClients.length;
        return {
            currentClientId: candidateClients[index].id,
            nextClientId: candidateClients[(index + 1) % clientLength].id,
        }
    }
    deltaOrder(clientId: string, delta: number) {
        const foundClient = _.find(this.context.clients, client => client.id === clientId);
        if(!foundClient) {
            console.log(`[deltaOrder] Can not find ${clientId}`);
            return this.context;
        }
        foundClient.order += delta;

        this.context.clients = this.context.clients.sort((a, b) => a.epic - b.epic || a.order - b.order);
        return this.context;
    }
    deltaEpic(delta: number) {
        this.context.epic += delta;
        return this.context;
    }
    deltaClientEpic(clientId: string, delta: number, order?: number) {
        const foundClient = _.find(this.context.clients, client => client.id === clientId);
        if(!foundClient) {
            console.log(`[deltaClientEpic] Can not find ${clientId}`);
            return this.context;
        }

        foundClient.epic += delta;
        if(_.isNumber(order)) {
            foundClient.order = order;
        }

        this.context.clients = this.context.clients.sort((a, b) => a.epic - b.epic || a.order - b.order);
        return this.context;
    }
    toggleEnable(clientId: string) {
        const foundClient = _.find(this.context.clients, client => client.id === clientId);
        if(!foundClient) {
            console.log(`[deltaOrder] Can not find ${clientId}`);
            return;
        }
        foundClient.enable = !foundClient.enable;
        return this.context;
    }
}

class MessageFactory {
    channel = '';
    clientId = '';
    constructor({channel, clientId} : MessageMeta) {
        this.channel = channel;
        this.clientId = clientId;
    }

    private getMeta(): MessageMeta {
        return {
            channel: this.channel,
            clientId: this.clientId,
        };
    }
    turnTo(clientId: string): string {
        const msg = new TurnToMessage({...this.getMeta(), turnToClientId: clientId});
        return msg.toString();
    }
}

class MessageDispatcher extends EventEmitter {
    next({topic, message}: {topic: string, message: any}) {
        let data;
        if( message instanceof object) {
            data = message
        } else {
            data = JSON.parse(message.toString());
        }

        if(!data.type) {
            this.emit('error', new Error('can not find message type'));
            return;
        }

        const type = data.type;
        this.emit('message', data);
        this.emit(type, data);
    }
}

interface IWebSocketProxyProps {
    onChangeClients?: (clients: Array<MqttClientMeta>) => void,
    onChangeGameState?: (clients: GameStateType) => void,
    onChangeThinkTimeOut?: (timeout: number) => void,
    onChangeDelayTimeOut?: (timeout: number) => void,
    onError?: (error: Error) => void,
    onTurnYou?: () => void,
    onNotTurnYou?: () => void,
    onTurnTo?: (clientId: string) => void,
    onConnected?: (mqttClient: mqtt.MqttClient) => void,
    onChangeConnected?: (connected: boolean) => void,
    onEmptyClientInEpic?: () => void,
    clientId: string,
    onChangeEpic?: (timeout: number) => void,
}

interface IWebSocketProxyState {
}

class WebSocketProxy extends Component<IWebSocketProxyProps, IWebSocketProxyState> {
    client?: mqtt.MqttClient = undefined;
    gameStateManager?: GameStateManager = undefined;
    messageFactory?: MessageFactory = undefined;
    messageDispatcher?: MessageDispatcher = undefined;
    channel: string = 'testchannel';

    constructor(props: any) {
        super(props);
    }

    close() {
        if(this.client) {
            if(this.client.connected) {
                this.client.end();
            }
            if(this.props.onChangeConnected) {
                this.props.onChangeConnected(false);
            }
        }
        this.client = undefined;
    }

    open(channel: string) {
        this.close();

        this.channel = channel;

        const clientId = this.props.clientId;

        const gameStateManager = new GameStateManager({channel, clientId});
        const messageFactory = new MessageFactory({channel, clientId});
        const dispatcher = new MessageDispatcher();

        // Mqtt connection
        const client = mqtt.connect('wss://aud.ssp.hinet.net:443/mqtt', {keepalive: KEEP_ALIVE, clientId});
        client.on('connect',  () => {
            client.subscribe(channel, (err) => {
                if (err) {
                    console.error(err);
                }
            });
            if(this.props.onConnected) {
                this.props.onConnected(client);
            }
            if(this.props.onChangeConnected) {
                this.props.onChangeConnected(client.connected);
            }
        });
        client.on('message', function (topic, message) {
            dispatcher.next({topic, message});
            // console.log(`topic = ${topic}, message = ${message}`)
        });


        // Event handler
        dispatcher.on(MessageType.gameStateChange, (data) => {
            // init game state
            const newState = new GameContextMessage(data);

            gameStateManager.loadCurrentState(newState);
            console.log('==============================');
            console.log('Channel by : ' + gameStateManager.context.channel);
            console.log('thinkTimeOut : ' + ms(gameStateManager.context.thinkTimeOut));
            console.log('delayTimeOut : ' + ms(gameStateManager.context.delayTimeOut));
            console.log('Host by : ' + gameStateManager.context.getHost());
            console.log('Game state : ' + gameStateManager.context.state);
            console.log('Epic : ' + gameStateManager.context.epic);
            console.log('Current : ' + gameStateManager.context.currentClientId);
            console.log('Clients : \n' + gameStateManager.context.clients.map(client => `${client.enable} - (${client.epic},${client.order}) - ${client.name} - ${client.id}`).join('\n'));
            console.log('==============================');

            if(this.props.onChangeThinkTimeOut) {
                this.props.onChangeThinkTimeOut(gameStateManager.context.thinkTimeOut);
            }
            if(this.props.onChangeDelayTimeOut) {
                this.props.onChangeDelayTimeOut(gameStateManager.context.delayTimeOut);
            }
            if(this.props.onChangeClients) {
                this.props.onChangeClients(gameStateManager.context.clients);
            }
            if(this.props.onChangeGameState) {
                this.props.onChangeGameState(gameStateManager.context.state);
            }
            if(this.props.onTurnTo) {
                this.props.onTurnTo(gameStateManager.context.currentClientId);
            }
            if(this.props.onChangeEpic) {
                this.props.onChangeEpic(gameStateManager.context.epic);
            }

            // dispatch game state
            if(gameStateManager.context.state === GameStateType.play) {
                dispatcher.emit(GameStateType.play);
            }
            if(gameStateManager.context.state === GameStateType.ready) {
                dispatcher.emit(GameStateType.ready);
            }
            if(gameStateManager.context.state === GameStateType.pause) {
                dispatcher.emit(GameStateType.pause);
            }
            if(gameStateManager.context.state === GameStateType.end) {
                dispatcher.emit(GameStateType.end);
            }
        });

        dispatcher.on(MessageType.turnTo, (data) => {
            const msg = new TurnToMessage(data);
            if(this.props.onTurnTo) {
                this.props.onTurnTo(msg.turnToClientId);
            }

            if(this.props.clientId === msg.turnToClientId) {
                // console.log('turn to ==> ' + this.props.clientId);
                if(this.props.onTurnYou) {
                    this.props.onTurnYou();
                }
            } else {
                if(this.props.onNotTurnYou) {
                    this.props.onNotTurnYou();
                }
            }
        });

        // helper
        this.client = client;
        this.gameStateManager = gameStateManager;
        this.messageFactory = messageFactory;
        this.messageDispatcher = dispatcher;
    }

    join(clientId: string, clientName: string) {
        if(this.gameStateManager && this.gameStateManager.context.state !== GameStateType.ready) {
            const error = new Error('Only join client for "ready" state');
            console.error(error);
            if(this.props.onError) {
                this.props.onError(error);
            }
            return;
        }

        if(this.client && this.gameStateManager) {
            const state = this.gameStateManager.joinClient(clientId, clientName);
            this.client.publish(this.channel, state.toString(), {qos:2, retain: true});
        }
    }

    exit(clientId: string) {
        if(this.client && this.gameStateManager) {
            const state = this.gameStateManager.exitClient(clientId);
            this.client.publish(this.channel, state.toString(), {qos:2, retain: true});
        }
    }

    play() {
        if(this.client && this.gameStateManager && this.messageFactory) {
            const context = this.gameStateManager.changeToPlay();
            this.client.publish(this.channel, this.messageFactory.turnTo(context.currentClientId), {qos:2});
            this.client.publish(this.channel, context.toString(), {qos:2, retain: true})
        }
    }

    next() {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.context;

            let info = undefined;
            try {
                info = this.gameStateManager.getTurnInfo();
            } catch (e) {
                console.error(e);
            }

            if(info) {
                if(this.messageFactory && info && context.currentClientId === '') { // First
                    context.currentClientId = info ? info.currentClientId : '';
                    this.client.publish(this.channel, this.messageFactory.turnTo(context.currentClientId), {qos:2});
                } else if(this.messageFactory && info &&  context.currentClientId !== info.nextClientId) { // in playing
                    context.currentClientId = info ? info.nextClientId : '';
                    this.client.publish(this.channel, this.messageFactory.turnTo(context.currentClientId), {qos:2});
                } else if(this.messageFactory && info &&  context.currentClientId && (context.currentClientId === info.nextClientId)) { // in playing
                    context.currentClientId = info ? info.nextClientId : '';
                    this.client.publish(this.channel, this.messageFactory.turnTo(context.currentClientId), {qos:2});
                }
            } else { // no client in the epic
                if(this.props.onEmptyClientInEpic) {
                    this.props.onEmptyClientInEpic();
                }
            }

            // Change game context
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true});
        }
    }

    pause() {
        if(this.client && this.gameStateManager) {
            this.gameStateManager.changeToPause();
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    end() {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.changeToEnd();
            context.currentClientId = '';
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    changeTimeout({thinkTimeOut, delayTimeOut} : {thinkTimeOut: number, delayTimeOut: number}) {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.changeTimeout({thinkTimeOut, delayTimeOut});
            this.client.publish(this.channel, context.toString(), {qos:2, retain: true})
        }
    }

    ready() {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.changeToReady();
            context.currentClientId = '';
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    deltaOrder(clientId: string, delta: number) {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.deltaOrder(clientId, delta);
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    deltaEpic(delta: number) {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.deltaEpic(delta);
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    toggleEnable(clientId: string) {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.toggleEnable(clientId);
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    deltaClientEpic(clientId: string, delta: number) {
        if(this.client && this.gameStateManager) {
            const context = this.gameStateManager.deltaClientEpic(clientId, delta);
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    nextClientEpic(clientId: string, delta: number) {
        if(this.client && this.gameStateManager) {
            const nextEpic  = this.gameStateManager.context.epic + delta;
            const candidateClients = this.gameStateManager.context.clients.filter(client => client.enable && (nextEpic === client.epic));
            const orders = candidateClients.map(client => client.order);

            let max = _.isEmpty(orders) ? 0 : _.max(orders);
            max = max || 0;
            const order = max + DEFAULT_ORDER;

            const context = this.gameStateManager.deltaClientEpic(clientId, delta, order);
            this.client.publish(this.channel, this.gameStateManager.getStateMessage(), {qos:2, retain: true})
        }
    }

    render() {
        return (
            <div className="websocket-proxy">
                {this.props.children}
            </div>
        );
    }
}

export default WebSocketProxy;
